.footer {
  background-color: var(--light-gray);
  color: var(--dark-desaturated-blue);
}

.footer__inner {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 185px;
  justify-content: space-between;

  padding-top: 65px;
  padding-bottom: 52px;


  @media (max-width: $max-tablet) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
    row-gap: 38px;
    column-gap: 30px;

    padding: 19px 0 35px 0;
  }

  @media (max-width: $max-mobile) {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    column-gap: 0;
    row-gap: 0;

    padding-top: 46px;
    padding-bottom: 0;
  }
}

.footer h3 {
  margin-bottom: 25px;

  font-family: $roboto-condensed;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  @media (max-width: $max-mobile) {
    margin-bottom: 0;
  }
}

.footer__icon {
  grid-column: 1/2;

  margin-right: 20px;
  max-width: 230px;
  width: 100%;

  @media (max-width: $max-tablet) {
    grid-column: 1/3;
    grid-row: 1/2;

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-right: 0;
    padding-bottom: 25px;
    max-width: 100%;

    border-bottom: 1px solid var(--grayish-green);
  }

  @media (max-width: $max-mobile) {
    flex-direction: column;
    justify-content: center;
    padding-bottom: 46px;
  }
}

.footer__years {
  display: none;

  @media (max-width: $max-tablet) {
    display: block;
    padding-left: 20px;

    font-size: 13px;
    line-height: 24px;

    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: var(--dark-desaturated-blue);

    opacity: 0.5;
  }

  @media (max-width: $max-mobile) {
    padding-left: 4px;
  }
}

.footer__text-wrapper {
  border-top: 1px solid var(--grayish-green);
}

.footer__text-block {
  grid-row: 2/3;
  grid-column: 1/-1;

  display: grid;
  grid-template-columns: 230px 360px 1fr 195px;

  align-items: center;
  flex-wrap: wrap;

  min-height: 52px;

  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  opacity: 0.5;

  @media (max-width: $min-desktop) {
    grid-template-columns: 230px 330px 1fr 195px;
  }

  @media (max-width: $max-tablet) {
    grid-template-columns: auto auto;
    min-height: 69px;
  }

  @media (max-width: $max-mobile) {
    display: flex;
    justify-content: center;
    flex-direction: column;

    padding-top: 28px;
    padding-bottom: 5px;
  }
}

.footer__text-block p {
  &:first-child {
    @media (max-width: $max-tablet) {
      display: none;
    }
  }

  &:nth-child(2) {
    @media (max-width: $max-tablet) {
      display: none;
    }
  }

  &:last-child {
    @media (max-width: $max-tablet) {
      justify-self: flex-end;
    }
  }

  @media (max-width: $max-mobile) {
    margin-bottom: 21px;
  }
}
